#main-app-container {
  height: 100vh;
}
#main-app-container .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 50px;
  padding: 8px;
  padding-left: 200px;
}
#main-app-container .header .right {
  display: flex;
  align-items: center;
}
#main-app-container .header .left {
  display: flex;
  align-items: baseline;
}
#main-app-container .header .left h4 {
  margin-left: 8px;
}
#main-app-container .header .left * {
  color: white;
}
.trigger {
  font-size: 18px;
  margin: 3px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  position: absolute !important;
  z-index: 1;
  top: -3px;
  left: -3px;
}
